import React, { useState, useEffect } from "react"
import CourseDataRow from "./curriculumTableRow"
import SearchComponent from "./searchComponent"

function CourseSelectionMenu({ setCourseSelected, userSelectedCourses }) {

    const [currentCourses, setCurrentCourses] = useState([]) // ç•¶å‰ç¯©é¸å™¨çš„çµæœ
    const [showedCourses, setShowedCourses] = useState([]) // é¡¯ç¤ºè¢« keyword å’Œ ç”¨æˆ¶é¸æ“‡éçš„ éæ¿¾æ‰çš„çµæœ
    const [isLoading, setIsLoading] = useState(true)
    const [isError, setIsError] = useState(false)
    const [queryString, setQueryString] = useState('?æ ¡å€=è˜­æ½­æ ¡å€&èª²ç¨‹ä¿®åˆ¥=å¿…ä¿®')
    const [keyword, setKeyword] = useState('')
    const [isShowedConflictedCourse, setIsShowedConflictedCourse] = useState('true')
    const [semesterYear, setSemesterYear] = useState('')

    const COURSE_SELECTION_API = 'https://ncyu-courseapi.azurewebsites.net/course_selection'

    const toCourseKeywords = (course) => {
        return course['ä¸Šèª²å­¸åˆ¶'] + course['èª²ç¨‹åç¨±'] + course['ä¸Šèª²ç³»æ‰€'] + course['æˆèª²è€å¸«'] + course['é¸èª²é¡åˆ¥']
    }

    
    const toCourseId = (course) => {
        return course['é–‹èª²ç³»è™Ÿ'] + course['é–‹èª²åºè™Ÿ'] + course['æ°¸ä¹…èª²è™Ÿ']
    }
    
    useEffect(() => {
        let userSelectCourseIds = userSelectedCourses.map(course => toCourseId(course))
        let newShowedCourses = currentCourses.filter(course => !userSelectCourseIds.includes(toCourseId(course)))
        newShowedCourses = newShowedCourses.filter(course => toCourseKeywords(course).toLowerCase().includes(keyword.toLowerCase()))
        if (!isShowedConflictedCourse) {
            newShowedCourses = newShowedCourses.filter(course => !isOverlapWithUserSelectedCourses(course))
        }
        setShowedCourses(newShowedCourses)

    }, [currentCourses, keyword, userSelectedCourses, isShowedConflictedCourse])

    useEffect(() => {
        setCurrentCourses([])
        setIsLoading(true)
        fetch(COURSE_SELECTION_API + queryString, { method: 'GET' }).then(
            response => response.json()
        ).then(
            data => {
                setCurrentCourses(data['result'])
                setSemesterYear(data['semester'])
                setIsLoading(false)
                setIsError(false)
            }
        ).catch(
            e => {
                setIsLoading(false)
                setIsError(true)
            }
        )
    }, [queryString])

    const isOverlap = (course1, course2) => {

        const CLASS_MAP = {
            '1': 1,
            '2': 2,
            '3': 3,
            '4': 4,
            'F': 5,
            '5': 6,
            '6': 7,
            '7': 8,
            '8': 9,
            '9': 10,
            'A': 11,
            'B': 12,
            'C': 13,
            'D': 14
        }
        for (let i = 0; i < course1['ä¸Šèª²æ™‚é–“'].length; ++i) {
            for (let j = 0; j < course2['ä¸Šèª²æ™‚é–“'].length; ++j) {
                if (course1['ä¸Šèª²æ™‚é–“'][i]['æ˜ŸæœŸ'] === course2['ä¸Šèª²æ™‚é–“'][j]['æ˜ŸæœŸ']) {
                    if (CLASS_MAP[course1['ä¸Šèª²æ™‚é–“'][i]['é–‹å§‹ç¯€æ¬¡']] <= CLASS_MAP[course2['ä¸Šèª²æ™‚é–“'][j]['çµæŸç¯€æ¬¡']] && CLASS_MAP[course2['ä¸Šèª²æ™‚é–“'][j]['é–‹å§‹ç¯€æ¬¡']] <= CLASS_MAP[course1['ä¸Šèª²æ™‚é–“'][i]['çµæŸç¯€æ¬¡']]) {
                        return true
                    }
                }
            }
        }

        return false;
    }

    const isOverlapWithUserSelectedCourses = (course) => {
        for (let i = 0; i < userSelectedCourses.length; ++i) {
            if (isOverlap(course, userSelectedCourses[i])) {
                return true
            }
        }
        return false
    }

    const handleAddSelectedCourse = (newCourse) => {
        const newSelectedCourses = [...userSelectedCourses]
        newSelectedCourses.push(newCourse)
        setCourseSelected(newSelectedCourses)
    }

    return (
        <div className="rounded course-selection-menu shadow-sm">
            <SearchComponent onFilterChange={setQueryString} onKeywordChange={setKeyword} setShowConflitedCheckValue={setIsShowedConflictedCourse} semesterYear={semesterYear} />
            <div className="table-wrapper-scroll-y custom-scrollbar">
                <table className="table table-striped non-border align-middle table-first-row-white">
                    <tbody>
                        <tr className="position-sticky top-0 blur-background">
                            <th>æ ¡å€</th>
                            <th>å¹´ç´š</th>
                            <th>ä¸Šèª²ç³»æ‰€</th>
                            <th>èª²ç¨‹åç¨±</th>
                            <th>è€å¸«</th>
                            <th>å­¸åˆ†æ•¸</th>
                            <th>ä¸Šèª²æ™‚é–“</th>
                            <th>é¸æ“‡</th>
                        </tr>
                        {
                            showedCourses.map((course) =>
                                    <CourseDataRow key={course['é–‹èª²ç³»è™Ÿ'] + course['é–‹èª²åºè™Ÿ'] + course['æ°¸ä¹…èª²è™Ÿ']} course={course} onUserSelect={handleAddSelectedCourse} isDisabled={isOverlapWithUserSelectedCourses(course)} />
                            )
                        }

                    </tbody>
                </table>
                {isLoading ?
                    <div>
                        <div className="spinner-grow" role="status">
                            <span className="visually-hidden">æ­£åœ¨è¼‰å…¥è³‡æ–™...</span>
                        </div>
                        <span className="fs-3 ms-3">è¼‰å…¥ä¸­...</span>
                        <br />
                        <span className="text-muted">(è‹¥è¼‰å…¥æ™‚é–“å¾ˆé•·ï¼Œé€šå¸¸ä»£è¡¨å¾Œç«¯åœ¨å¾ä¼‘çœ åˆ°èµ·åºŠ)</span>
                    </div>
                    : ""}
                {isError ? <span className="fs-3">ç™¼ç”ŸéŒ¯èª¤</span> : ""}
                {!isError && !isLoading && showedCourses.length === 0 ? <span className="fs-3">æŸ¥ç„¡çµæœ</span> : ""}
            </div>

        </div>
    )
}

export default CourseSelectionMenu